.skills {
  background-color: #181818;
  color: white;
  padding: 80px 20px;
  text-align: center;
}

.skills-title {
  font-size: 1.8rem;
  margin: 2.5rem 0 1.5rem;
  color: gold;
  font-weight: bold;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0.75rem 1rem;
  list-style: none;
  padding: 0;
  margin: 0 auto 2.5rem;
  max-width: 1000px;
}

.skill-item {
  display: flex;
  align-items: center;
  gap: 8px;
  background-color: #2c2c2c;
  padding: 0.6rem 1.2rem;
  border-radius: 20px;
  font-size: 0.95rem;
  color: #fff;
  transition: transform 0.2s ease, background-color 0.2s ease;
}

.skill-item:hover {
  transform: scale(1.05);
  background-color: #3a3a3a;
}

.skill-icon {
  width: 20px;
  height: 20px;
  object-fit: contain;
}
